<template>
  <div>
    <div id="header">
        <h1>Home</h1>
        <hr>
    </div>
    <v-card
    class="mx-auto"
    max-width="434"
    tile
  >
    <v-img
      height="100%"
      src="https://cdn.vuetifyjs.com/images/cards/server-room.jpg"
    >
      <v-row
        align="end"
        class="fill-height"
      >
        <v-col
          align-self="start"
          class="pa-0"
          cols="12"
        >
          <v-avatar
            class="profile"
            color="grey"
            size="164"
            tile
          >
            <v-img src="https://cdn.vuetifyjs.com/images/profiles/marcus.jpg"></v-img>
          </v-avatar>
        </v-col>
        <v-col class="py-0">
          <v-list-item
            color="rgba(0, 0, 0, .4)"
            dark
          >
            <v-list-item-content>
              <v-list-item-title class="text-h6">
                Marcus Obrien
              </v-list-item-title>
              <v-list-item-subtitle>Network Engineer</v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>
        </v-col>
      </v-row>
    </v-img>
  </v-card>
    <div v-for="courses in info.results" v-bind:key="courses.url" id="courses">
        <div class="Course_Name">
            {{ courses.Course_Name }} {{ courses.Department}}
        </div>
    </div>
    <div id="footer">
        <p>CourseEvalSys.com</p>
    </div>
  </div>
</template>

<script>
    import axios from 'axios';

    export default {
        mode: 'history',
        name: 'App',
        data: function () {
            return {
                info: ''
            }
        },
        mounted() {
            axios
                .get('http://localhost:8080/api/course')
                .then(response => (this.info = response.data))
        }
    }
</script>

<style>
    #courses {
        padding: 10px;
    }

    .Course_Name {
        font-size: large;
        font-weight: bolder;
        color: black;
        text-decoration: none;
        padding: 5px 0 5px 0;
    }
    #header {
        text-align: center;
        margin-top: 20px;
    }
    #footer {
        position: fixed;
        left: 0;
        bottom: 0;
        height: 50px;
        width: 100%;
        background: whitesmoke;
        text-align: center;
        font-weight: bold;
    }
</style>




